body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: Arial;
  padding: 20px;
  background: #f1f1f1;
  }
  .leftcolumn {
  /* float: left; */
  margin-left: auto;
  margin-right: auto;
  width: 75%;
  }
  .card {
  background-color: white;
  padding: 20px;
  margin-top: 20px;
  }
  .title {
  margin-left: 50px;
  margin-right: 50px;
  }
  .content {
  margin-left: 30px;
  margin-right: 30px;
  margin-top: 70px;
  }
  h1 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 40px;
  line-height: 1.5;
  }
  p {
  font-family: 'Times New Roman', Times, serif;
  font-size: 20px;
  line-height: 1.8;
  }
  .row:after {
  content: "";
  display: table;
  clear: both;
  }
